@import url(https://fonts.googleapis.com/css2?family=Crimson+Text&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Crimson+Text&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Cairo&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.subpage-header {
    margin-top: 5%;
    margin-bottom: 5%;
    text-align: center;
}

.subpage-text {
    margin-top: 5%;
    margin-left: 15%;
    margin-right: 15%;
    text-align: center;
    font-size: 1.125rem;
}

.post-header {
    margin-top: 2%;
    margin-bottom: 2%;
    text-align: center;
}

.project {
    display: flex;
    justify-content: left;
}

.contact-icons {
    margin-top: 5%;
    margin-bottom: 5%;
    text-align: center;
    font-size: 3rem;
}

.contact-icons a {
    padding: 2%;
}

#page-center {
    /* height: 100%; <-- This makes footer underlap content, but */
    /* min-height: 100%; */
    min-height: 70vh;
}

#oops-container{
    margin-top: 20%;
    text-align: center;
}

html, body {
    height: 100%;
    padding: 0;
    margin: 0;
}

html {
  position: relative;
  min-height: 100%;
}

body {
    font-family: 'Crimson Text', serif !important;
    /* font-family: 'Cairo', sans-serif !important; */
    font-size: 100%;
    background-image: url(/website/static/media/interlaced.2a71914d.png);
}

/* Main Page */

.row {
    margin: 0;
}

.col-md {
    padding: 0;
}

.container-fluid {
    padding: 0;
}

/* Navbar */
a h2 {
    padding-left: 15px;
    color: rgb(255, 255, 255);
}

#nav-title {
    text-decoration: none;
}

.nav-link {
    color: white;
    text-decoration: none;
}

.nav-item .active {
    background-color: lavender;
    color: rgb(25, 50, 75);
    opacity: 1;
}

.nav-link {
    transition: 0.3s;
}

.nav-link:hover {
    background-color: lavender;
    color: rgb(25, 50, 75);
    opacity: 1;
}

nav {
    background-color: rgb(25, 50, 75);
}

#homepage {
    background-image: url(/website/static/media/lake.aaa37e7d.jpg);
    background-size: cover;
    background-position: right;
    height: 100vh;
}

/* Left side carousel */
.homepage-left {
    display: flex;
    padding: 0 !important;
}

/* Right side text */
#maintext-container {
    /* padding: 10% 5%; */
    padding-left: 12%;
    padding-right: 50%;
    padding-top: 8%;
    text-align: center;
    color: lavender;
}

/* Footer Design */
footer {
    background-color: rgb(25, 50, 75);
    /* position: static; */
    /* width: 100%; */
    bottom: 0;
    margin-bottom: 0;
    height: 100px;
}

.footer-copyright{
    color: white;
}

footer a {
    padding: 5px;
}

/* styling */
hr.separator {
    overflow: visible;
    padding: 0;
    /* border: none; */
    border-top: medium double #333;
    color: #333;
    text-align: center;
    width: 50%;
}

p {
    font-size: 1.125rem;
}

.horizontal {
    display: flex;
    align-items: center;
    /* justify-content: space-between; */
    justify-content: space-around;
    flex-direction: row;
    width: 100%;
    height: 100%;
    overflow: clip;
}


/* .content {
    min-height: calc(100vh - 100px);
} */

/* fun */
.fun-button {
    background-color: lavender; /* Green */
    border: none;
    color: rgb(25, 50, 75);
    padding: 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
    width: 25%;
}

.counter {
    margin: 5%;
    width: 90%;
    display: flex;
    /* text-align: center; */
    align-items: center;
    justify-content: space-between;
}

.swearjar {
    display: flex;
    align-items: center;
    flex-direction: column;
}

.num-display {
    display: flex;
    flex-direction: column;
    align-items: center;
}

